.slidershadow{
    width: 99%;
    margin-bottom: 50px;
    align-items: center;
  
    overflow: hidden;
 
  position: relative;
}
.slidershadow::before{
    content: '';
    z-index: 5;
    width: 50px;
    height: 100%;
   position: absolute;
   box-shadow: 0 0 20px rgba(0,0,0,0.5);

    padding: 20px;
    background-color: var(--bgcolor);
}
.slidershadow::after{
    content: '';
    z-index: 5;
    box-shadow: 0 0 20px rgba(0,0,0,0.5);
    top: 0;
    right: 0;
    width: 50px;
    height: 100%;
   position: absolute;
    padding: 20px;
    background-color: var(--bgcolor);
}
.slick-center .Services_card{


    opacity: 1;
    scale: 1.1;


}

.Services_card{

    background-color: var(--cardbg);

    margin: 20px;
    z-index: 1;
overflow: hidden;
display: flex;
justify-content: center;
align-items: center;
    opacity: .6;
    height: 300px;
    border-radius: 25px;
 
}

.Services_card img{
    min-width: 100%;


    object-position: center;
}
@media only screen and (max-width:1032px) {
    .slidershadow::before,.slidershadow::after{
        display: none;
    }
}